import React, { useState, useMemo, useEffect } from 'react'
import immutable from 'seamless-immutable';

import { Form, Button } from "antd";

import ajv, { toErrorSchema } from "./ajv";
import useData from './hooks/useData';
import { getDefaultFormState } from "./utils/getDefaultFormState";
import deepEquals from './utils/deepEquals';

import getWidgets from './utils/getWidgets'
import Cpts, { FormItem } from './components';

const index = ({ schema, data: _data, customFields = {}, onChange, onSubmit, onError, uiSchems = {}, children }) => {
    if (!onChange) {
        throw new Error(`onChange`);
    }
    let widgets = getWidgets(customFields)
    const [state, setState] = useData({ errors: {}, data: {}, validateAll: false, isValidata: false })
    let { errors, data, validateAll, isValidata } = state
    const validateData = (data) => {
        let validate = ajv.compile(schema);
        let res = validate(data)
        let errors = {}
        if (!res) {
            errors = toErrorSchema(validate.errors)
        }
        return { res, errors, data }
    }
    useEffect(() => {
        let data = getDefaultFormState(schema, _data)
        if (!deepEquals(data, _data)) {
            onChange(data)
        } else {
            let { errors } = validateData(data)
            onError && onError(errors)
            setState({ data, errors })
        }
    }, [_data])
    const handleChange = (d) => {
        setState({ isValidata: true })
        onChange(d)
    }
    const handleSubmit = e => {
        e.preventDefault()
        let res = validateData(data)
        setState({ data, errors: res.errors, validateAll: true })
        onSubmit && onSubmit(res)
    }
    const formItemLayout = {
        labelCol: { span: 6 },
        wrapperCol: { span: 18 }
    };
    const btnItemLayout = {
        wrapperCol: { offset: 6, span: 18 }
    };
    return <div style={{ width: '800px' }}>
        <Form {...formItemLayout} onSubmit={handleSubmit}>
            {Object
                .keys(schema.properties)
                .map(key => {
                    let inputProps = {
                        key,
                        schema: schema.properties[key],
                        uischem: uiSchems[key] || {},
                        widgets,
                        error: errors[key],
                        name: key,
                        value: data[key],
                        validateAll,
                        isValidata,
                        onChange: handleChange
                    }
                    return <Cpts {...inputProps} />
                })}
            {children ? children :
                <FormItem {...btnItemLayout}>
                    <Button type="primary" htmlType="submit"></Button>
                </FormItem>
            }
        </Form>
    </div>
}

export default index