import React from 'react';
import { Input } from 'antd';

const { TextArea } = Input;

const textArea = ({ name, value, disabled, onBlur, onFocus, schema, uischem, onChange, ...inputProps }) => {
    const handleChange = ({ target: { value } }) => {
        value = value === "" ? schema.emptyValue : value
        onChange({ [name]: value });
    };
    let { allowClear, ...otherOptions } = uischem.uiOptions || {}
    let uiOptions = {
        ...otherOptions,
        disabled: disabled
    }
    return <TextArea rows={4} value={value} onChange={handleChange}   {...inputProps} {...uiOptions} />
}

export default textArea;